.hero-img {
    height: 20vh;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-position:left center;
}

.heading {
    padding-left: 2.5rem;
    padding-top: 6%;
    position: relative;
    z-index: 1;
}

.hero-img h1 {
    font-size: 3.5rem;
}

.mask3::after {
    content: "";
    width: 100%;
    height: 20vh;
    position: absolute;
    top: 0;
    left: 0;
    background-color: black;
    opacity: 0.5;
}

.overlay {
    content: "";
    background: rgba(0, 0, 0, 0.5);
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -2;
}

/* for smaller desktop width*/
@media screen and (max-width:1440px){
    .hero-img h1 {
        font-size: 2.5rem;
    }
    .heading {
        padding-left: 2.5rem;
        padding-top: 7%;
        position: relative;
        z-index: 1;
    }
}
